/** axios **/
import axios from "axios";
import findUrl from "./setUrl";

/**  axios ***/
const httpService = axios.create({
  baseURL: process.env.BASE_API, // 
  /**  **/
  timeout: 3000, // 
});

/** request **/
httpService.interceptors.request.use(
  (config) => {
    /** token- **/
    // if (true) { // 
    /** token,    **/
    const token: any = sessionStorage.getItem("token");
    config.headers = {
      token,
    };
    // }
    return config;
  },
  (error) => {
    /**  **/
    Promise.reject(error);
  }
);

/**  **/

/**  get
 *  url:
 *  params:
 **/
export function get(url: any, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: findUrl(url),
      method: "get",
      params: params,
    })
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

/**  post
 *  url:
 *  params:
 **/
export function post(url: any, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: findUrl(url),
      method: "post",
      data: params,
    })
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

/**  
 *  url:
 *  params:
 **/
export function upload(
  url: any,
  params = {
    url: undefined,
  }
) {
  return new Promise((resolve, reject) => {
    httpService({
      url: findUrl(url), // 
      method: "post",
      data: params.url, // 
    })
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

export default {
  get,
  post,
  upload,
};
